import React, { useEffect} from 'react';

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

const useStyles = makeStyles({
  "@keyframes highlight" : {
    from: { backgroundColor: '#fdff32' },
    to: { backgroundColor: 'none' },
    
  }, 
  table: {
    minWidth: '100%',
  },
  row: {
    fontSize:'1em',
    fontWeight:'600',
  },
  column: {
    fontSize:'1.2em',
    fontWeight:'bold',
  },
  tablecontainer : {
    border:'1px solid rgba(224, 224, 224, 1)'
  },
  trow : {
    "&:last-of-type" : {
      animation:'$highlight 10s ease-in-out'
    }
  }
});


export default function Names({data, isFetch, highlight, getAllVisitors}) {


  
  useEffect(() => {
  getAllVisitors()
  }, [isFetch])


  const classes = useStyles();
  function createData(id, name) {
    return { id, name };
  }
  const rows = data.map(i => createData(i.id, i.name))
  
  console.log('names')
    return (
        <div style={{marginTop:'30px', display:'flex', justifyContent:'center', alignItems:'center'}}>
           <div style={{width: '95%' }}>
      <TableContainer className={classes.tablecontainer}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell className={classes.column}>Sıra</TableCell>
            <TableCell className={classes.column} align="left">Ad Soyad</TableCell>
          </TableRow>
        </TableHead>
        <TableBody >
          {rows.map((row) => (
            <TableRow className={highlight ? classes.trow : classes.trow} key={row.id}>
              <TableCell className={classes.row} component="th" scope="row">
                {row.id}
              </TableCell>
              <TableCell className={classes.row} align="left">{row.name}</TableCell>           
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </div>
        </div>
    )
}
