import React, {useState, useEffect } from 'react';
import Heading from './Heading';
import Input from './Input';
import Names from './Names';
import Time from './Time';
import YourUsers from './YourUsers';
import Footer from './Footer';
import './App.css';

function App() {
  const [data, setData ] = useState([])
  const [state, setstate] = useState('')
  const [display, hide] = useState(false)
  const [highlight, setHighlight] = useState(false)
  const [dayOfWeek, setDay] = useState(0)
  const [countDown, setCount] = useState(0);
  const [isFetch, setFetch] = useState(0)
  const [isLoaded, setLoading] = useState(true)


  const setName = (e) => {
    setstate(e.target.value)
  }

  const getAllVisitors = () => {
    fetch(`${window.location.origin}/visitors`)
    .then(response => response.json())
    .then(names => setData(names))
  }

    const addUser = (e) => {
    setstate('')
    setCount(60)
    document.cookie = `${state}=${state}; max-age=36000;`
    hide(true)
    setHighlight(!highlight)
    setTimeout(() => {
      window.scrollTo(0, document.body.scrollHeight)
      hide(false)
    }, 1500);
    const visitorData = {id: data.length + 1, name:state}
    e.preventDefault()
    fetch(`${window.location.origin}/addVisitor`,{
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(visitorData)
    })
    .then(response => {return response.json()})
    .then(res => {
      setFetch(!isFetch)})
  }

  useEffect(() => {
  const d = new Date()
  let weekDay = d.getDay()
  setDay(weekDay)
  }, [])


  useEffect(() => {
      getAllVisitors()  
  }, [])

  useEffect(() => {
    setTimeout(() => {
      setLoading(false)
    }, 2000);
  }, [])

  return (
    <div>
      {isLoaded 
      ? <div style={{height:'100vh', width:'100vw',backgroundImage:' linear-gradient(to top, #9890e3 0%, #b1f4cf 100%)'}}> </div>
      : <div className="App">
     <Heading />
     <Time />
     <Input countDown = {countDown} setCount={setCount} dayOfWeek={dayOfWeek} addUser={addUser} state={state} setName={setName} display={display} />
     <YourUsers highlight={highlight}/>
     <Names data={data}  isFetch={isFetch} highlight={highlight} getAllVisitors={getAllVisitors} />
     <Footer />
    </div>
}
    </div>
   
  );
}

export default App;
