import React, { useEffect, useState } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import PersonOutlineIcon from '@material-ui/icons/PersonOutline';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {TextField, Button} from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      width: '95%',
      marginTop:'20px',
      marginLeft:'10px'
    },
    accord : {
      backgroundColor:'transparent'
    },
    demo: {
      backgroundColor: 'inherit',
    },
    title: {
      margin: theme.spacing(4, 0, 2),
      fontWeight:'700'
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      fontWeight: '900',
    },
    lItemText : {
      fontWeight:'900'
    },
    input : {
      width: '58%',
      height:'100%',
      marginRight:'.2em'
    },
    button : {
      width:'15%',
      height:'35px',
      fontWeight:'bold',
      fontSize:'.8em',
    },
  }));
  



export default function YourUsers({highlight}) {
    const [all, setall] = useState([])
    const classes = useStyles();
    const [dense, setDense] = React.useState(true);
    const [secondary, setSecondary] = React.useState(false);
    const [edit, setEdit] = useState(false)
    const [state, setstate] = useState('')
    
    const setName = (e) => {
      setstate(e.target.value)
    }

    useEffect(() => {
    let allUsers = document.cookie.split(';')
    setall(allUsers)
    }, [highlight])
    
    const deleteRecord = (currentName) => {
      document.cookie = currentName+'=; Max-Age=-99999999;'; 
      fetch(`${window.location.origin}/delete`,{
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({name:currentName})
      })
      .then(response => {return response.json()})
      .then(res => {console.log(res)})
    }
    const updateRecord = (currentName) => {
      document.cookie = currentName+'=; Max-Age=-99999999;'; 
      document.cookie = `${state}=${state};max-age=36000;`; 

      fetch(`${window.location.origin}/change`,{
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({name:currentName, newName:state})
      })
      .then(response => {return response.json()})
      .then(res => {console.log(res)})
    }
   
    return (
        <div className={classes.root}>
          <Accordion className={classes.accord}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>Imtina et / Dəyişiklik et</Typography>
        </AccordionSummary>
        <AccordionDetails>
        <Grid container spacing={2}>
        <Grid item xs={12} md={6}>
          <div className={classes.demo}>
            <List dense={dense}>
              {all[0] !== '' ? all.map((i,index) =>
              edit ? 
              <div>
              <TextField  value={state} className={classes.input} id="filled-basic" onChange={setName} />
              <Button type="sumbit" className={classes.button} onClick = {() => updateRecord(i.split('=')[0])} variant="outlined"  color="primary">təsdiq</Button>
              <Button type="sumbit" className={classes.button} onClick = {() => setEdit(false)}  color="primary" variant="outlined">Geri</Button>
              </div>
              :  <ListItem key={index}>
                  <ListItemAvatar>
                    <Avatar>
                      <PersonOutlineIcon />
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText
                    className={classes.lItemText}
                    primary={i.split('=')[0]}
                    secondary={secondary ? 'Secondary text' : null}
                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="delete" onClick = {() => deleteRecord(i.split('=')[0])}>
                      <DeleteIcon  />
                    </IconButton>
                    <IconButton edge="end" aria-label="edit" onClick = {() => setEdit(true)}>
                      <EditIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>,
              ): null}
            </List>
          </div>
        </Grid>
      </Grid>
        </AccordionDetails>
      </Accordion>      
     
        </div>
    )
}
