import React from 'react'

export default function Heading() {
    return (
        <div style={{display:'flex',flexDirection:'column', justifyContent:'center',alignItems:'center'}}>
        <h2 style={{ padding:'10px', lineHeight:'31px', textAlign:'center'}}>Əjdərbəy məscidi cümə namazına onlayn qeydiyyat</h2>
        <h4 style={{color:'#FF0000', padding:'15px',textAlign:'justify', lineHeight:'23px' }}>Qeydiyyat yalnız cümə günləri 10:00-dan sonra başlayır. Məscidə girişdə şəxsiyyət vəsiqəsi və COVİD sertifikat tələb olunur.</h4>
        </div>
    )
}
