import React, {useEffect} from 'react'
import {Button, TextField, makeStyles} from '@material-ui/core';
import LinearProgress from '@material-ui/core/LinearProgress';



const useStyles = makeStyles(() => ({
  main : {
    width:'99%'
  },
  form : {
  height:'100%'
  },
 input : {
   width: '75%',
   height:'100%'
 },
 button : {
   width:'20%',
   height:'55px',
   fontWeight:'bold',
   fontSize:'.8em',
   letterSpacing:'.2em'
 },
 progress: {
   margin:'15px',
   width:'90%'
 }
}));

export default function Input({dayOfWeek,addUser, setName, state, display, countDown, setCount }) {
  const classes = useStyles();
  

  useEffect(() => {
  let c = setTimeout(() => {
     setCount(prev => prev - 1 )
   }, 1000);
   if(countDown === 0) {
     clearTimeout(c)
     setCount(0)
   }
  }, [countDown])

    return (
        <div className={classes.main}>
        <form className={classes.form} onSubmit={addUser}>
        <TextField  value={state} className={classes.input} id="filled-basic" label="Ad soyad ata adı" variant="outlined" onChange={setName} />
        <Button disabled={countDown !== 0 ? true : false} type="sumbit" className={classes.button} variant="contained" color="primary">{countDown === 0 ? 'Yazıl' : countDown}</Button>
        </form>
        {display ? 
        <div className={classes.progress}>
        <LinearProgress />
        </div>
        : null }
        </div>
    )
}
