import React, {useState, useEffect} from 'react'

export default function Time() {
const [date, setdate] = useState('')
const months = [{id:0, name:'Yanvar'},{id:1, name:'Fevral'},{id:2, name:'Mart'},{id:3, name:'Aprel'},
{id:4, name:'May'},{id:5, name:'İyun'},{id:6, name:'İyul'},{id:7, name:'Avgust'},
{id:8, name:'Sentyabr'},{id:9, name:'Oktyabr'},{id:10, name:'Noyabr'},{id:11, name:'Dekabr'},]
useEffect(() => {
   const currentDate = new Date()
   const date = currentDate.getDate()
   const month = months.filter(i => i.id === currentDate.getMonth())[0].name
   const year = currentDate.getFullYear()
   setdate(`${date} ${month} ${year}`)
// eslint-disable-next-line react-hooks/exhaustive-deps
}, [])
    return (
        <div>
       <h2>{date}</h2>
        </div>
    )
}
